@import url(https://fonts.googleapis.com/css?family=VT323);
.overlay,
.neg,
.err-text,
.link {
  color: #e0e2f4;
}

.overlay {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background: rgba(51, 51, 51, 0.7);
  z-index: 10;
}

.overlay,
.err-text {
  font: normal 20px/1.25rem "VT323", monospace;
}

.neg {
  font: normal 2.75rem/1.05em "VT323", monospace;
}

.overlay {
  background: #0414a7;
}

.container {
  width: 90%;
  margin: auto;
  max-width: 640px;
}

.bsod {
  padding-top: 10%;
}
.bsod .neg {
  text-align: center;
  color: #0414a7;
}
.bsod .neg .bg {
  background: #aaaaaa;
  padding: 0 15px 2px 13px;
}
.bsod .title {
  margin-bottom: 50px;
}
.bsod .nav {
  margin-top: 35px;
  text-align: center;
}
.bsod .nav .link {
  text-decoration: none;
  padding: 0 9px 2px 8px;
}
.bsod .nav .link:hover,
.bsod .nav .link:focus {
  background: #aaaaaa;
  color: #0414a7;
}

.hero-box {
  width: 100%;
  height: 95vh;  
  background-color: #ffffff; 
  position: relative; 
  margin: 0;
  padding: 0; 
  text-align: center; 
}

.img-box {
  width: 33%;
  position: absolute;
  top: 50%;
  left: 50%; 
  transform: translate(-50%, -50%);  
  margin-left: 3.5rem;
} 

.stats-box {
  position: absolute;
  top: 27%;
  left: 15%;   
  width: 15%; 
}

@media only screen and (max-width: 1400px) {
  .stats-box {
    position: absolute;
    top: 30%;
    left: 10%;    
    width: 25%; 
    transform: scale(0.7);
  }
}
@media only screen and (max-width: 1000px) {
  .stats-box {
    position: absolute;
    top: 25%;
    left: 8%;    
    width: 30%; 
  }
}

.corona-img { 
width: 100%;
-webkit-animation: rotate 30s infinite linear, blendIn 0.2s ease-in;
        animation: rotate 30s infinite linear, blendIn 0.2s ease-in;
}

.hero-text {
transform: translate(1rem, 3rem);
} 
@media only screen and (max-width: 1000px) {
  .hero-text {
    transform: translate(1rem, 4rem) scale(0.7);
  }
} 

.sub-text {
  position: absolute;
  left: 50%;
  top: 90%; 
}

@-webkit-keyframes rotate {
  0% {
    transform: rotate(0);
  }
  
  100% {
    transform: rotate(360deg); 
  }
}

@keyframes rotate {
  0% {
    transform: rotate(0);
  }
  
  100% {
    transform: rotate(360deg); 
  }
}

@-webkit-keyframes blendIn {
  0% {
    opacity: 0;
  }
  
  100% {
    opacity: 1;
  }
}

@keyframes blendIn {
  0% {
    opacity: 0;
  }
  
  100% {
    opacity: 1;
  }
}
.container-main {
  background-repeat: no-repeat;
  height: 100%;
  background-position: center;
  background-attachment: fixed;
  background-image: linear-gradient(rgba(56, 56, 56, 0.856), rgba(0, 0, 0, 0.856)), url(/sharithg/senior-design-SW_miniproject.git/static/media/mask.fc354215.jpg);
  background-size: cover;  
}
